import Vue from 'vue'
import VueRouter from 'vue-router'

// 引入组件
import Home from '@/views/Home/Home.vue';
import Login from '@/views/Log/login.vue';
import List from '@/views/List/List.vue'
import ListGoods from '@/views/List/List_goods.vue'
import Xq from '@/views/List/xq.vue';
import Carts from '@/views/carts/carts.vue'
import Buy from '@/views/List/buy.vue'
import Settle from '@/views/List/settle.vue'
import Register from '@/views/Log/register.vue'

Vue.use(VueRouter)

const router = new VueRouter({
    routes: [
        { path: '/', redirect: '/Home' },
        { path: '/Home', component: Home },
        { path: '/login', component: Login },
        { path: '/register', component: Register },
        {
            path: '/List',
            component: List,
            children: [
                { path: '/List', redirect: '/Home/Goods' },
                { path: '/Home/Goods', component: ListGoods },
                {
                    path: '/Home/xq',
                    component: Xq,
                    children: [
                        { path: '/Home/xq', redirect: '/Home/xq/buy' },
                        { path: '/Home/xq/buy', component: Buy },
                        { path: '/Home/xq/settle', component: Settle }
                    ]
                },

            ]
        },
        { path: '/Carts', component: Carts },
    ]
})


export default router