<template>
  <div class="buy">
           <myButton></myButton>
     <!-- 表格 -->
    <el-row class="contianer title">
      <el-col :span="3">图片</el-col>
      <el-col :span="7">描述</el-col>
      <el-col :span="5">关键词</el-col>
      <el-col :span="2">单价</el-col>
      <el-col :span="3">数量</el-col>
      <el-col :span="2">删除</el-col>
      <el-col :span="2"> <el-checkbox  v-model="checkAll" @change="handleCheckAllChange">全选</el-checkbox></el-col>
    </el-row>
    <el-row class="contianer body" v-for="item in tableData" :key="item.id">
        <el-col :span="3"><img src="../../../public/images/mobile.png" alt=""></el-col>
      <el-col :span="7">Apple Macbook Air 13.3英寸笔记本电脑 银色（Corei5）处理器/8GB内存</el-col>
      <el-col :span="5">`颜色: 银色<br>
                 处理器: Core I5<br>
                 内存: 8GB<br>
                 尺寸: 13.3英寸</el-col>
      <el-col :span="2" style="color: #B1191A; font-size:14px; font-weight: 700;">888.00</el-col>
      <el-col :span="3"> <el-input-number v-model="item.num" size="small" :min="1" :max="10" @change="checkNum"></el-input-number></el-col>
      <el-col :span="2" style="color: #B1191A;">删除</el-col>
      <el-col :span="2">
       <input type="checkbox" class="checkbox" v-model="item.checked" @change="checkedChange">
      </el-col>
    </el-row>
    <!-- 结算 -->
    <el-row class="settle contianer">
        <span class="span1">已选中 <i>{{checkedNumber}}</i> 件商品</span>
        <span>总价: {{allPrice}}</span>
        <el-button type="success" @click="open">结算</el-button>
       </el-row>
 <!-- 猜你喜欢 -->
 <el-menu class="el-menu-demo contianer like" active-text-color="#ffd04b" 
 background-color="#545c64"  text-color="#fff" mode="horizontal" style="margin:15px auto;">
  <el-menu-item index="1">猜你喜欢</el-menu-item>
   <el-menu-item index="1">处理中心</el-menu-item>
  </el-menu>
<!-- 轮播图 -->
<div class="swiper contianer">
  <el-carousel :interval="3000" type="card" height="300px">
    <el-carousel-item v-for="item in 4" :key="item">
       <div class="box">
           <img src="../../../public/images/mobile.png" alt="">
          <div class="right">
             <p>iPhone 6处理器采用64位A8处理器和M8协处理器；Wi-Fi速度是以前的3倍；摄像头800万像素，每个像素大小1.5微米，最大光圈ƒ/2.2，采用数码图像防抖</p>
             <el-button type="success" class="el-icon-shopping-cart-2">加入购物车</el-button>
          </div>
       </div>
    </el-carousel-item>
  </el-carousel>
</div>
  </div>
</template>

<script>
export default {
 data(){
        return{
          checkAll:false,  
          checkedNumber:0, 
          allPrice:0,         
              tableData: [{id:1,checked:true, num: 1,},{id:2,checked:false, num: 1,},{id:3,checked:true, num: 1,},{id:4,checked:true, num: 1,},],
        }
    },

  methods: {
    checkNum(){
     this.checkedNumber=0
      this.allPrice=0
       this.tableData.forEach((item) => {
           if(item.checked){            
          this.checkedNumber+=item.num
          this.allPrice+=item.num*888
           }
       })
    },
     handleCheckAllChange() {
       this.tableData.forEach((item) => {
          item.checked=this.checkAll
       })
        this.checkNum()
      },
    checkedChange(){ 
      this.checkNum()
      let a= this.tableData.every((item) => {         
            return item.checked                  
       })
       this.checkAll=a
    },
     open() {
       this.$router.push('settle')    
      }
    
    },
    created(){
 this.checkNum()
    },

    
  
}
</script>

<style lang="less" scoped>
.buy{
    .title{
    height: 36px;
    .el-col{
      text-align: center;
      line-height: 36px;
    }
  }
  .body{
    height: 110px;
      .el-col{
      height: 110px;
      display: flex;
      justify-content: center;
      align-items: center;

      img{
        height: 80%;
        width: auto;
       
      }
    .checkbox{
      width: 15px;
      height: 15px;
    }
    }
  }
  // 结算
  .settle{
    height: 60px;
    display: flex;
    justify-content: end;
    align-items: center;
       margin: 15px auto !important;
    span{
      color: #B1191A;
      font-size: 16px;
         margin: auto 15px;
    }
    .span1{
       font-size: 16px;
       color: #333333; 
       i{
          color: #B1191A;
       }
    }
    /deep/.el-button{
     height: 100%;
     width: auto;
    }
  }
   // 猜你喜欢
   .like{
      height: 40px !important;
      li{
        height: 100%;
        line-height: 40px;
      }
    }
    .swiper{
      height: 320px;
      margin-bottom: 100px !important;
      .box{
        height: 100%;
        width: 100%;
        display: flex;
        justify-content: center;
        background: #F2F6FC;
        .right{
          display: flex;
          flex-direction: column;
          justify-content: space-around;
          box-sizing: border-box;
          padding: 35px;
          p{
            font-size: 16px;
          }
        }
      }
    }
}
</style>