import Vue from 'vue'
// 导入APP根组件
import App from './App.vue'
// 导入路由
import router from './router'
// 全局组件
import myNav from '@/components/repeat/nav/nav.vue'
import mySearch from '@/components/repeat/search/MySearch.vue'
import myFooter from '@/components/repeat/footer/Footer.vue'
import rightSide from '@/components/repeat//side/right_side.vue'
import myButton from '@/components/repeat/button/button.vue'

// 引入全局样式
import '@/assets/css/public.css'
// 导入element ui及其样式文件
import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';


Vue.config.productionTip = false

Vue.use(ElementUI)


Vue.component('myNav', myNav)
Vue.component('mySearch', mySearch)
Vue.component('myFooter', myFooter)
Vue.component('rightSide', rightSide)
Vue.component('myButton', myButton)






new Vue({
    router,
    render: h => h(App)
}).$mount('#app')